/* eslint-disable no-unused-expressions */
import React, { useEffect, useState } from 'react';
import styles from './Slide.module.css';
import { ReactComponent as TrashIcon } from './Trash.svg';

const Slide = ({ data = [], onChange, onDelete }) => {
    const [active, setActive] = useState(0);
    const [datas, setDatas] = useState([]);

    useEffect(() => {
        const dataFiltered = [
            ...data?.filter(
                (x) =>
                    typeof x.img !== 'undefined' &&
                    x.img !== null &&
                    x.img !== '',
            ),
        ];
        if (JSON.stringify(datas) !== JSON.stringify(dataFiltered)) {
            setDatas(dataFiltered);
            setActive(0);
        }
    }, [data]);

    const onElementClicked = (element, index) => {
        onChange?.(element, index);
        setActive(index);
    };

    const onDeleteClicked = (element, index) => {
        setActive(index);
        onDelete(element, index);
    };

    return (
        <div className={styles.root}>
            {datas?.map?.((content, index) => {
                if (content.img !== '') {
                    const className =
                        index === active
                            ? `${styles.root__img_area} ${styles.root__active}`
                            : `${styles.root__img_area}`;
                    return (
                        <div className={styles.root__img} key={index}>
                            <div className={styles.root__img_title}>
                                {content.title}
                            </div>
                            <div
                                className={className}
                                onClick={() => {
                                    onElementClicked(content, content.id);
                                }}
                                style={{
                                    backgroundImage: `url(${content.img})`,
                                    backgroundRepeat: 'no-repeat',
                                    backgroundSize: 'cover',
                                }}
                            />
                            <div className={styles.root__delete}>
                                <TrashIcon
                                    className={styles.root__delete__icon}
                                    onClick={() =>
                                        onDeleteClicked(content, content.id)
                                    }
                                />
                            </div>
                        </div>
                    );
                }
            })}
        </div>
    );
};

export default Slide;
