/* eslint-disable no-unused-expressions */
/* eslint-disable no-unused-vars */
/* eslint-disable camelcase */
import React, { Fragment, useEffect, useRef, useState } from 'react';
import styles from './styles.module.css';
import CaptureButton from './component/CaptureButton';
import Slide from './component/Slide';
import Button from './component/Button';
import { ReactComponent as BackIcon } from './Chevron-left.svg';
import Proptypes from 'prop-types';

const App = ({ data, scale, quality, type, onSave, onClose }) => {
    const streamx = useRef();
    const datas = useRef([
        ...data?.map?.((dt, index) => {
            return {
                ...dt,
                id: index,
            };
        }),
    ]);
    const canvas_ref = useRef();
    const camera_view_ref = useRef();
    const focal_ref = useRef();
    const previewbody_ref = useRef();
    const [imgCaptured, setImgCaptured] = useState([]);
    const [focalBorder, setFocalBorder] = useState([20, 20, 20, 20]);
    const [imgPreSrc, setImgPreSrc] = useState(null);
    const [imgPreSrcIndex, setImgPreSrcIndex] = useState(0);
    const [mode, setMode] = useState('Capture');
    const [currentImgIndex, setCurrentImgIndex] = useState(0);
    const [canCapture, setCanCapture] = useState(false);

    useEffect(() => {
        onLoadCameraStream();
        onGetNextImage();

        return () => {
            streamx.current?.getTracks()?.forEach?.((track) => {
                track.stop();
            });
        };
    }, []);

    const onLoadCameraStream = () => {
        const camDef = {
            video: {
                width: { ideal: 1920 },
                height: { ideal: 1080 },
                facingMode: 'environment',
            },
            audio: false,
        };
        navigator.mediaDevices
            .getUserMedia(camDef)
            .then((stream) => {
                streamx.current = stream;
                camera_view_ref &&
                    camera_view_ref.current &&
                    (camera_view_ref.current.srcObject = stream);

                setCanCapture(true);
            })
            .catch(console.error);
    };

    const onImageCaptured = () => {
        if (mode !== 'Capture') {
            onConfirmationAccept(imgPreSrcIndex);
            setMode('Capture');
            onLoadCameraStream();
            onGetNextImage();
            return;
        }
        if (imgCaptured.length >= data.length) {
            return;
        }

        const camWidth = camera_view_ref?.current?.videoWidth;
        const camHeight = camera_view_ref?.current?.videoHeight;
        const ctx = canvas_ref.current.getContext('2d');

        const [xRatio, yRatio, wRatio, hRatio] = onGetRatioBwFocalAndPage();

        let ratio = scale;
        let ql = quality;
        if (
            typeof ratio === 'undefined' ||
            ratio === null ||
            ratio === '' ||
            isNaN(ratio) ||
            typeof ratio !== 'number' ||
            ratio === 'undefined'
        ) {
            ratio = 1;
        }

        if (
            typeof ql === 'undefined' ||
            ql === null ||
            ql === '' ||
            isNaN(ql) ||
            typeof ql !== 'number' ||
            ql === 'undefined'
        ) {
            ql = 1;
        }

        const xCutting = xRatio * camWidth;
        const yCutting = yRatio * camHeight;
        const hCutting = hRatio * camHeight;
        const wCutting = wRatio * camWidth;

        canvas_ref.current.width = wCutting * ratio;
        canvas_ref.current.height = hCutting * ratio;

        ctx.filter = 'brightness(150%)';

        ctx.drawImage(
            camera_view_ref.current,
            xCutting,
            yCutting,
            wCutting,
            hCutting,
            0,
            0,
            wCutting * ratio,
            hCutting * ratio,
        );

        const tmp_captured = [...imgCaptured];
        tmp_captured.push(canvas_ref.current.toDataURL('image/jpeg', ql));

        setImgCaptured([...tmp_captured]);
        datas.current[currentImgIndex].img = canvas_ref.current.toDataURL(
            'image/jpeg',
            ql,
        );
        onGetNextImage();
        const filterd_capture = datas?.current?.filter(
            (x) =>
                typeof x.img !== 'undefined' && x.img !== null && x.img !== '',
        ).length;
        if (filterd_capture === datas.current?.length) {
            setImgPreSrc(datas?.current?.[0]?.img);
            streamx.current?.getTracks()?.forEach?.((track) => {
                track.stop();
            });
            setMode('Preview');
        }
    };

    const onGetNextImage = () => {
        for (let i = 0; i < datas.current.length; i++) {
            if (
                typeof datas?.current?.[i]?.img === 'undefined' ||
                datas?.current?.[i]?.img === ''
            ) {
                setCurrentImgIndex(i);
                onInterpolateFocal(datas.current[i].type);
                return;
            }
        }
    };

    const onGetRatioBwFocalAndPage = () => {
        const bounding = focal_ref?.current?.getBoundingClientRect?.();
        const { width, height } = bounding;
        const xRatio = focalBorder[3] / width;
        const yRatio = focalBorder[0] / height;
        const wRatio = (width - focalBorder[3] - focalBorder[1]) / width;
        const hRatio = (height - focalBorder[0] - focalBorder[2]) / height;
        return [xRatio, yRatio, wRatio, hRatio];
    };

    const onInterpolateA4Paper = (ref) => {
        const { innerWidth, innerHeight } = window;
        const pFittingHeigth = innerHeight - 80;
        let iHeight = pFittingHeigth;

        let iWidth = (iHeight * 210) / 297;

        if (iWidth + 20 > innerWidth) {
            iWidth = innerWidth - 20;
            iHeight = (iWidth * 297) / 210;
        }

        iHeight = iHeight - 60;

        const bLeft = (innerWidth - iWidth) / 2;
        const bRight = (innerWidth - iWidth) / 2;

        const bTop = 88;

        const bBottom = innerHeight - iHeight - bTop;
        setFocalBorder([bTop, bRight, bBottom, bLeft]);
    };

    const onInterpolateCard = () => {
        const { innerWidth, innerHeight } = window;
        const pFittingWidth = innerWidth - innerWidth * 0.05;
        const iWidth = pFittingWidth - 20;

        const iHeight = (iWidth * 54) / 86;

        const bLeft = (innerWidth * 0.95 - iWidth) / 2 + 25;
        const bRight = (innerWidth * 0.95 - iWidth) / 2 + 25;
        const bTop = (innerHeight * 0.85 - iHeight) / 2;
        const bBottom = innerHeight - iHeight - bTop;
        setFocalBorder([bTop, bRight, bBottom, bLeft]);
    };

    const onInterpolateFocal = (sType) => {
        switch (sType) {
            case 'Paper':
                return onInterpolateA4Paper();
            case 'ID':
            default:
                return onInterpolateCard();
        }
    };

    const onImageCapturedClicked = () => {
        setImgPreSrc(datas?.current?.[0]?.img);
        streamx.current?.getTracks()?.forEach?.((track) => {
            track.stop();
        });
        setMode('Preview');
    };

    const onPreviewImageChanged = (element, index) => {
        setImgPreSrcIndex(index);
        setImgPreSrc(element.img);
    };

    const onGoBackToCapture = () => {
        setMode('Capture');
        onLoadCameraStream();
        onGetNextImage();
    };

    const onConfirmationAccept = (index) => {
        const cloneImgCaptured = [...imgCaptured];
        cloneImgCaptured.splice(index, 1);
        datas.current[index].img = '';
        if (cloneImgCaptured?.length > 0) {
            setImgCaptured(cloneImgCaptured);
            setImgPreSrc(cloneImgCaptured[0]);
        } else {
            setImgCaptured([]);
            setMode('Capture');
            onLoadCameraStream();
            onGetNextImage();
        }
    };

    const onRequestDeleteImage = (img, index) => {
        setImgPreSrc(img);
        onConfirmationAccept(index);
    };

    const onSaveImgCaptured = () => {
        onSave?.([...datas.current]);
    };

    const onDeleteAllImageCapturedClicked = () => {
        datas?.current?.map?.((datax) => {
            datax.img = '';
        });
        setImgCaptured([]);
        setMode('Capture');
        onLoadCameraStream();
        onGetNextImage();
    };

    const RenderCapture = () => {
        return (
            <div className={styles.app}>
                <div className={styles.app__camera_postaction}>
                    <div
                        className={styles.app__camera_postaction__cancel}
                        onClick={onClose}
                    >
                        <BackIcon />
                        Trở về
                    </div>
                    <div className={styles.app__camera_postaction__title}>
                        {datas?.current?.[currentImgIndex]?.title}
                    </div>
                </div>
                <canvas
                    ref={canvas_ref}
                    className={styles.app__camera_sensor}
                />
                <video
                    ref={camera_view_ref}
                    className={styles.app__camera_view}
                    autoPlay
                    playsInline
                />
                <div className={styles.app__camera_action}>
                    {canCapture && (
                        <div className={styles.app__camera_capture}>
                            <CaptureButton
                                onCapture={onImageCaptured}
                                content='Chụp ảnh'
                            />
                        </div>
                    )}
                    {imgCaptured?.length > 0 && (
                        <div className={styles.app__camera_paper}>
                            <Button content='Lưu' onClick={onSaveImgCaptured} />
                        </div>
                    )}

                    {imgCaptured?.length > 0 && (
                        <div className={styles.app__camera_output}>
                            <div className={styles.app__camera_output__length}>
                                {imgCaptured?.length}
                            </div>
                            <Button
                                content='Xem lại'
                                onClick={onImageCapturedClicked}
                                style='outline'
                            />
                        </div>
                    )}
                </div>
                <div
                    className={styles.app__focalArea}
                    ref={focal_ref}
                    style={{
                        borderTopWidth: focalBorder?.[0] || 20,
                        borderRightWidth: focalBorder?.[1] || 20,
                        borderBottomWidth: focalBorder?.[2] || 20,
                        borderLeftWidth: focalBorder?.[3] || 20,
                    }}
                >
                    <div className={styles.app__scanner} />
                    Vui lòng chỉnh tài liệu khớp với khung hình
                </div>
            </div>
        );
    };

    const RenderPreview = () => {
        return (
            <Fragment>
                <div className={styles.preview}>
                    <div className={styles.preview__header}>
                        <div
                            className={styles.preview__header__back}
                            onClick={onGoBackToCapture}
                        >
                            <BackIcon />
                            Trở về
                        </div>
                        <div className={styles.preview__header__slide}>
                            <Slide
                                data={[...datas.current]}
                                onChange={onPreviewImageChanged}
                                onDelete={onRequestDeleteImage}
                            />
                        </div>
                    </div>
                    <div className={styles.preview__title}>
                        {datas?.current?.[imgPreSrcIndex]?.title}
                    </div>
                    <div className={styles.preview__body} ref={previewbody_ref}>
                        <div
                            className={styles.preview__body__img}
                            style={{
                                backgroundImage: `url(${imgPreSrc})`,
                                backgroundRepeat: 'no-repeat',
                                backgroundSize: 'contain',
                                backgroundPosition: 'center',
                            }}
                        />
                    </div>
                    <div className={styles.app__camera_action}>
                        <div className={styles.app__camera_capture}>
                            <CaptureButton
                                content='Chụp lại'
                                onCapture={onImageCaptured}
                            />
                        </div>
                        <div className={styles.app__camera_paper}>
                            <Button content='Lưu' onClick={onSaveImgCaptured} />
                        </div>

                        {imgCaptured?.length > 0 && (
                            <div className={styles.app__camera_output}>
                                <Button
                                    content='Xoá hết'
                                    onClick={onDeleteAllImageCapturedClicked}
                                    style='outline'
                                />
                            </div>
                        )}
                    </div>
                </div>
            </Fragment>
        );
    };

    return (
        <Fragment>
            {mode === 'Capture' ? RenderCapture() : RenderPreview()}{' '}
            <div
                style={{
                    position: 'absolute',
                    top: 0,
                    right: 0,
                    zIndex: 999,
                    color: 'white',
                }}
            >
                v1.0.11
            </div>
        </Fragment>
    );
};

App.propTypes = {
    data: Proptypes.array.isRequired,
};

export default App;
