/* eslint-disable no-unused-expressions */
import React from 'react';
import styles from './CaptureButton.module.css';

const CaptureButton = ({ content, onCapture }) => {
    const onTouching = () => {};

    const onTouchEnd = () => {
        onCapture?.();
    };

    return (
        <div onTouchStart={onTouching} onTouchEnd={onTouchEnd}>
            <div className={styles.root__outer_circle}>
                <div className={styles.root__inner_circle} />
            </div>
            <div className={styles.root__tool_tip}>{content}</div>
        </div>
    );
};

export default CaptureButton;
